@import url('https://fonts.googleapis.com/css2?family=Unbounded:wght@300;500;700;900&display=swap');

*{
  font-family: 'Unbounded', cursive;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App{
  display: flex;
  min-width: 450px;
  width: 50%;
  flex-direction: column;
  margin: 2rem auto;
  box-shadow: 0.1rem 0.2rem 0.1rem 0.2rem silver;
  color: white;
  padding: 1rem;
  background-image: linear-gradient(to top, #5f72bd 0%, #9b23ea 100%);
  text-align: center;
}
.filter{
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.3rem;
  padding: 1rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  background-image: linear-gradient(-225deg, #3D4E81 0%, #5753C9 48%, #6E7FF3 100%);
}
table{
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}
table,tr,td,th{
  border:2px solid black;
  padding: 0.3rem;
  border-collapse: collapse;
  margin: auto;
}
.error{
  display: flex;
  background-color: bisque;
  border: 2px solid black;
  font-weight: bold;
  font-size: 1.4rem;
  padding: 1rem;
  color: red;
  transition-duration: 0.5s;
}
.green{
  display: flex;
  background-color: bisque;
  border: 2px solid black;
  font-weight: bold;
  font-size: 1.4rem;
  padding: 1rem;
  color: green;
  transition-duration: 0.5s;
}
.goaway{
  display: none;
}
input{
  width: 145px;
  padding: 0.5rem;
}

#psubmit{
  padding: 0.3rem 0.3rem;
  border: 0;
  width: 200px;
  margin: auto;
  grid-column: 1/3;
  font-weight: bold;
  font-size: 1.3rem;

}
.pform{
  display: grid;
  grid-template-columns: repeat(2,1fr);
  grid-template-rows: repeat(3,1fr);
  place-content: center;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  background-image: linear-gradient(-225deg, #3D4E81 0%, #5753C9 48%, #6E7FF3 100%);
}

.persons{
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(200, 200, 220, 1) 0px 8px 16px -8px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
